
  import axios from 'axios'
  import {defineComponent} from 'vue';

  export default defineComponent ({
      name:'homeView',
      data(){
        return{
          loading: false,
          lastPokemon: 1,
          pokemons:[] as any[any],
          colors:{
            grass: 'bg-green-500',
            fire: 'bg-orange-500',
            water:'bg-sky-400',
            bug:'bg-lime-500',
            electric:'bg-yellow-500',
            normal:'bg-gray-400',
            poison:'bg-violet-400',
            ground:'bg-amber-600',
            fairy:'bg-rose-300',
            fighting:'bg-orange-700',
            psychic:'bg-pink-600',
            rock:'bg-yellow-700',
            ghost:'bg-purple-600',
            ice:'bg-cyan-300',
            dragon:'bg-red-500'
          } as any[any]
        }
      },
      mounted(){
        this.getPokemons()
      },
      methods:{
        async getPokemons(){
          this.loading = true

          const limit = this.lastPokemon + 9 // 20

          while( this.lastPokemon <= limit && this.lastPokemon <= 151){
            let response = await axios.get('https://pokeapi.co/api/v2/pokemon/'+this.lastPokemon)
            this.pokemons.push(response.data)
            this.lastPokemon++
            if(this.lastPokemon == limit){
              this.loading = false
            }
          }
          
        }
        // getColor(type:string){
